import common from '../../../common/common.js';
var mThis;
export default {
	data() {
		return {
			username: '',
			password: ''
		};
	},
	methods: {
		jumpUrl() {
			uni.navigateTo({
				url: '../register/register'
			});
		},
		login() {
			if (this.username.length == 0) {
				this.showMessage('请输入账号');
				return;
			}
			if (this.password.length == 0) {
				this.showMessage('请输入密码');
				return;
			}
			uni.showLoading({
				complete() {
					uni.request({
						method: 'POST',
						data: {
							username: mThis.username,
							password: mThis.password
						},
						header:{
							'Content-Type':'application/x-www-form-urlencoded'
						},
						dataType: 'json',
						url: 'https://www.wanandroid.com/user/login',
						success: res => {
							console.log(res);
							if (res.data.data != null) {
								mThis.saveLoginInfo(res);
								mThis.onBackLastPage();
							} else {
								mThis.showMessage(res.data.errorMsg);
							}
						},
						fail: error => {
							console.error(error);
						},
						complete() {
							uni.hideLoading();
						}
					});
				}
			});
		},
		showMessage(title) {
			uni.showToast({
				title: title,
				icon: 'none',
				duration: 2000
			});
		},
		saveLoginInfo(res) {
			uni.setStorageSync(common.user_login, JSON.stringify(res.data.data));
			this.$store.state.login = JSON.stringify(res.data.data);
		},
		onBackLastPage() {
			uni.navigateBack({
				delta: 1
			});
		}
	},
	onLoad() {
		mThis = this;
	}
};
